<template>
  <div class="page-wrapper">
    <div class="word">{{ name }}</div>
    <swiper-view :list="cardList" />
    <van-button class="btn-lottery" type="primary" @click="startLottery"></van-button>
    <card-box :list="[1,2,3]" />
    <van-popup class="popup-wrapper" v-model="showPopup">
      <card-lottery v-if="showPopup" />
    </van-popup>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import Swiper from '@/components/Swiper'
import CardBox from '@/components/CardBox'
import CardLottery from '@/components/CardLottery'
import { Button, Popup } from 'vant'
const imgExample = require('../../assets/example.jpg')

export default {
  name: 'Swiper',
  components: {
    'swiper-view': Swiper,
    'card-box': CardBox,
    'card-lottery': CardLottery,
    'van-button': Button,
    'van-popup': Popup
  },
  data() {
    return {
      name: 'Swiper',
      showPopup: false,
      cardList: [
        {
          id: 1,
          img: imgExample,
          text: ''
        },
        {
          id: 2,
          img: imgExample,
          text: ''
        },
        {
          id: 3,
          img: imgExample,
          text: ''
        },
        {
          id: 4,
          img: imgExample,
          text: ''
        }
      ]
    }
  },
  methods: {
    startLottery() {
      this.showPopup = true
    }
  }
}
</script>

<style lang="less" scoped>
  @import '../../style/common.less';
  .page-wrapper {
    width: ~`unit(750)`;
    .word {
      color: white;
      font-size: ~`unit(48)`;
      background: green;
    }
    .swiper-container {
      margin-top: ~`unit(16)`;
    }
    .card-box-wrapper {
      margin-top: ~`unit(48)`;
    }
    .btn-lottery {
      margin-top: ~`unit(32)`;
    }
    .popup-wrapper {
      background: none;
      overflow: visible;
    }
  }
</style>



